import Vue from 'vue'
import Router from 'vue-router'
//
import Zuye from '@/page/zuye'
//
import Feilei from '@/page/feilei'
// 
import Mudidi from '@/page/mudidi'
//
import Chujingyou from '@/page/chujingyou'
//
import Guoleiyou from '@/page/guoleiyou'
//
import Wode from '@/page/wode'
//
import Wode_weidengru from '@/page/wode_weidengru'
//
import Soushuo from '@/page/soushuo'
//
import Wodexinxi from '@/page/wodexinxi'
//
import Wode_yhq from '@/page/wode_yhq'
//
import Wode_dd from '@/page/wode_dd'
//
import Dengru from '@/page/dengru'
//
import Zhaohuimima from '@/page/zhaohuimima'
//
import Zhuche from '@/page/zhuche'

Vue.use(Router)

export default new Router({
	linkActiveClass:'Colore',
  	routes: [
    {
     	path: '/',
      	redirect:'/dengru',
    },

    {
      	path:'*',
      	redirect:'/dengru',
    },
    {
    	path:'/Dengru',
    	component:Dengru,
    },
    {
    	path:'/Zhaohuimima',
    	component:Zhaohuimima,
    },
    {
    	path:'/Zhuche',
    	component:Zhuche,
    },
    {
    	path:'/Zuye',
    	component:Zuye,
    },
    {
    	path:'/Feilei',
    	component:Feilei,
    },
    {
        path:'/Guoleiyou/:eviceId',
        name:'/Guoleiyou',
        component:Guoleiyou,
    },
    {
    	path:'/Mudidi',
    	component:Mudidi,
    },
    {
    	path:'/Wode',
    	component:Wode,
    },
    {
    	path:'/Wode_weidengru',
    	component:Wode_weidengru,
    },
    {
    	path:'/Soushuo',
    	component:Soushuo,
    },
    {
    	path:'/Wode/Wodexinxi',
    	component:Wodexinxi,
    },
    {
    	path:'/Wode/Wode_yhq',
    	component:Wode_yhq,
    },
    {
    	path:'/Wode/Wode_dd',
    	component:Wode_dd,
    },
    {
    	path:'/Chujingyou/:deviceId',
    	name:'Chujingyou',
    	component:Chujingyou,
    }

  ]
})



